import React, { useState } from 'react'

function SelectGameDropdown({game, setGame}) {
    const [isActive, setIsActive] = useState(false);

    const GameNames = {
        "endless-siege-2": 'Endless Siege',
        "candy-fiesta": 'Candy Fiesta',
        "templok": 'Templok',
        "ringy-dingy": 'RingyDingy',
        "rug-pool": 'Rug Pool',
        "maze": 'The Maze'
    }

    return (
        <div className={`dropdown mt-0 mb-0 ${isActive ? 'is-active' : ''}`}>
            <div className="dropdown-trigger">
                <button className="custom-button" aria-haspopup="true" style={{minWidth: '140px' }} onClick={() => {setIsActive(!isActive)}} aria-controls="dropdown-menu">
                    <span>{game !== undefined ? GameNames[`${game}`] : 'All Games'}</span>
                    <div className={`arrow ${!isActive ? 'down' : ''}`}></div>
                </button>
            </div>
            <div className="dropdown-menu" id="dropdown-menu" role="menu">
                <div className="dropdown-content" style={{maxWidth: '140px', minHeight: '214px', overflow: 'hidden'}}>
                    <a className={`dropdown-item ${game === undefined ? 'is-active' : ''}`} onClick={() => {
                        setGame(undefined)
                        setIsActive(!isActive)
                    }} >
                        All Games
                    </a>
                    <a className={`dropdown-item ${game === 'endless-siege-2' ? 'is-active' : ''}`} onClick={() => {
                        setGame('endless-siege-2')
                        setIsActive(!isActive)
                    }} >
                        Endless Siege
                    </a>
                    <a className={`dropdown-item ${game === 'candy-fiesta' ? 'is-active' : ''}`} onClick={() => {
                        setGame('candy-fiesta')
                        setIsActive(!isActive)
                    }} >
                        Candy Fiesta
                    </a>
                    <a className={`dropdown-item ${game === 'templok' ? 'is-active' : ''}`} onClick={() => {
                        setGame('templok')
                        setIsActive(!isActive)
                    }} >
                        Templok
                    </a>
                    <a className={`dropdown-item ${game === 'ringy-dingy' ? 'is-active' : ''}`} onClick={() => {
                        setGame('ringy-dingy')
                        setIsActive(!isActive)
                    }} >
                        RingyDingy
                    </a>
                    <a className={`dropdown-item ${game === 'rug-pool' ? 'is-active' : ''}`} onClick={() => {
                        setGame('rug-pool')
                        setIsActive(!isActive)
                    }} >
                        Rug Pool
                    </a>
                    <a className={`dropdown-item ${game === 'maze' ? 'is-active' : ''}`} onClick={() => {
                        setGame('rug-pool')
                        setIsActive(!isActive)
                    }} >
                        The Maze
                    </a>
                </div>
            </div>
        </div>
    )
}

export default SelectGameDropdown